/*==========  Desktop First Method  ==========*/

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1199px) {
    .box_menu{
        margin: 0 0 6px 0;
    }
    #menu{
        ul{
            display: none;
            height: auto;
            position: absolute;
            top: 53px;
            left: 17px;
            background: #313231;
            z-index: 1;
            li{
                display: block;
                margin: 0;
                padding: 5px 20px;
            }
        }
    }
    #hamburger{
        display: inline-block;
        background: none;
        color:#fff;
        border:0;
        font:bold 15px/20px Arial,Helvetica,sans-serif;
        cursor:pointer;
        outline:none;
    }
    .social_links{
        clear: none;
        display: inline-block;
        margin: 2px 0 0 0;
    }
    .cont_wrapper{
        padding: 0 17px;
    }
    .main_block_cont{
        width: 100%;
    }
    #carousel{
        .slr_cont{
            font: 12px/18px Arial,Helvetica,sans-serif;
            padding: 5px 10px 30px 15px;
            left: 290px;
            width: 215px;
            height: auto;
            h1{
                font: 18px/30px "Myriad Pro",Arial,Helvetica,sans-serif; 
            }
            h2{
                font: 28px/30px "Myriad Pro",Arial,Helvetica,sans-serif;
                margin: 0 0 5px 0;
            }
        }
        .owl-pagination{
            bottom: 5px;
            right: 127px;
        }
        .owl-page{
            width: 12px;
            height: 12px;
            margin: 0 12px 0 0;
            .owl-numbers{
                display: none;
            }
        }
    }
    .cont_title{
        h1{
            font: 22px/26px "Trebuchet MS Regular",Arial,Helvetica,sans-serif;
        }
    }
    #tabs{
        .post_list{
            text-align: center;
            li{
                width: 43.406%;
                margin: 0 30px 20px 0;
            }
            & li:nth-child(2n){
                margin: 0 0 10px 0;
            }
            & li:nth-child(3n+3){
                margin: 0 30px 20px 0;
            }
            & li:last-child{
                margin: 0 0 20px 0;
            }
        }
    }
    .sb_sld_box{
        h2{
            font: 17px/22px "Trebuchet MS",Arial,Helvetica,sans-serif;
        }
    }
    .search{
        img{
            right: 4px;
        }
        input[type="search"]{
            width: 120px;
            padding: 7px 30px 7px 10px;
        }
    }
    .bx-wrapper{
        .bx-controls{
            .bx-prev{
                left: -19px;
            }
            .bx-next{
                right: -39px;
            }
        }
    }
    .sb_blk_photo{
        .box_photo{
            li{
                &:nth-child(2n+1){
                    margin: 0 3px 23px 0;
                }
            }
        }
    }
    .sign_up_block{
        padding: 15px;
        h1{
            margin: 0 20px 0 0;
        }
        form{
            input[type="text"]{
                width: 128px;
                padding: 8px 3px 8px 9px;
            }
            input[type="submit"]{
                padding: 6px 10px;
                right: 24px;
            }
        }
    }
    .box_post{
        &:last-child{
            margin: 0 0 60px 0; 
        }
    }
    #categor-block{
        #tabs-1,
        #tabs-2{
            header{
                padding: 12px 30px 10px 30px;
            }
        }
    }
}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
    #carousel{
        margin-bottom: 40px;
        .slr_cont{
            left: 247px;
            font: 12px/20px Arial,Helvetica,sans-serif;
            padding: 5px 5px 3px 15px;
            h1{
                font: 18px/30px "Myriad Pro",Arial,Helvetica,sans-serif;
                color: #fff;
            }
            h2{
                font: 28px/30px "Myriad Pro",Arial,Helvetica,sans-serif;
                color: #ee5b46;
                margin: 0 0 5px 0;
            }
        }
        .owl-pagination{
            bottom: -25px;
            right: 193px;
        }
        .owl-page{
            width: 12px;
            height: 12px;
            .owl-numbers{
                display: none;
            }
        }
    }
    .cont_title{
        header{
            h1{
                font: 20px/26px "Trebuchet MS Regular",Arial,Helvetica,sans-serif;
            }
        }
    }
    #tabs{
        .post_list{
            li{
                width: 46.406%;
                margin: 0 10px 30px 0;
            }
            & li:nth-child(3n+3){
                margin: 0 10px 30px 0;
            }
            & li:last-child{
                margin: 0 0 20px 0;
            }
        }
    }
    .search{
        img{
            right: -2px;
        }
        input[type="search"]{
            width: 115px;
            padding: 7px 27px 7px 10px;
        }
    }
    .sign_up_block{
        h1{
            font: 19px/28px "Trebuchet MS",Arial,Helvetica,sans-serif;
        }
    }
    #categor-block{
        .box_categor{
            padding: 25px 0 18px 0;
            li{
                width: 49%;
                margin-bottom: 30px;
            }
            & li:nth-child(2n+2){
                border-right: none;
            }
        }
        .list_categor{
            li{
                width: 100%;
                margin-bottom: 0;
            }
        }
    }
}

/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {
    .container{
        margin: 0 10px;
    }
    .tel_num{
        margin: -40px 0 22px 0;
    }
    #nav{
        float: none;
        margin: 0 0 20px 0;
        ul{
            display: table;
            width: 100%;
            li{
                display: table-cell;
                text-align: center;
                vertical-align: top;
            }
        }
    }
    .cont_wrapper{
        margin: 0 10px 30px 10px;
    }
    .main_cont_wrap{
        width: 100%;
    }
    #carousel{
        .slr_cont{
            display: none;
        }
    }
    #tabs{
        .post_list{
            li{
                width: 100%;
                margin: 0 0px 30px 0;
                text-align: center;
                padding: 0 100px;
            }
            & li:nth-child(3n+3){
                margin: 0 0px 20px 0;
            }
            & li:last-child{
                margin: 0 0 20px 0;
            }
        }
    }
    .sign_up_block{
        h1{
            display: block;
            margin: 0 0 20px 0;
            text-align: center;
        }
        form{
            display: block;
            text-align: center;
            float: none;
            input[type="text"]{
                width: 268px;
            }
        }
    }
    .block_posts{
        margin-bottom: 30px;
    }
    #menu-tabs{
        margin: 0;
    }
    #categor-block{
        .box_categor{
            li{
                width: 100%;
                //margin-bottom: 30px;
                text-align: center;
                padding: 0 70px;
                border-right: none;
            }
        }
        .list_categor{

        }
    }
    .sp_cr{
        float: none;
        text-align: center;
        margin: 0 0 15px 0;
    }
    .ftr_nav_box{
        text-align: center;
    }
    .footer_nav{
        ul{
            float: none;
            li{
                margin: 0 15px 0 0;
            }
        }
    }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {
    .tel_num{
        margin: -17px 0 16px 0;
    }
    #carousel{
        .owl-pagination{
            bottom: -30px;
            right: 123px;
        }
    }
    .cont_title{
        header{
            text-align: center;
        }
    }
    #tabs{
        ul{
            li{
                display: table-row;
            }
        }
        .post_list{
            li{
                padding: 0 25px;
            }
        }
    }
    .sign_up_block{
        form{
            input[type="text"]{
                width: 228px;
                margin-bottom: 10px;
            }
            input[type="submit"]{
                border-radius: 3px;
                float: none;
                text-align: center;
                margin-bottom: 10px;
            }
        }
    }
    #menu-tabs{
        ul{
            display: table;
            width: 100%;
            li{
                display: table-row;
            }
        }
    }
    #categor-block{
        ul{
            display: table;
            width: 92%;
            li{
                display: table-row;
                text-align: center;
            }
        }
        .box_categor{
            width: 100%;
        }
        .list_categor{
            width: 100%;
            li{
                padding: 0;
                
            }
        }
    }
}

