        border-radius: 10px !important;
    }
    .slr_cont{
        position: absolute;
        font: 14px/20px Arial,Helvetica,sans-serif;
        color: #fff;
        top: 0;
        left: 463px;
        width: 234px;
        background: rgba(42,31,21,0.7);
        height: 266px;
        padding: 15px 10px 0 15px;
        border: 0px solid #000;
        border-radius: 0 10px 10px 0;
        h1{
            font: 22px/35px "Myriad Pro",Arial,Helvetica,sans-serif;
            color: #fff;
            margin: 0;
        }
        h2{
            font: 32px/35px "Myriad Pro",Arial,Helvetica,sans-serif;
            color: #ee5b46;
            margin: 0 0 10px 0;
        }
        p{
            margin: 0;
        }
    }
    .owl-page{  //button
        padding: 0px 0px;
        margin: 0 3px;
        background: #6b685e;
        color: #fff;
        font:bold 12px/35px Arial,Helvetica,sans-serif;
        border-radius: 20px !important;
        .owl-numbers{
            padding: 0 9.5px;
            background: none;
            margin: 0;
            line-height: 25px;
            box-shadow: inset 2px -2px 0px 0px rgba(178, 171, 165, 0.9);
            -moz-box-shadow: inset 2px -2px 0px 0px rgba(178, 171, 165, 0.9);
            -webkit-box-shadow: inset 2px -2px 0px 0px rgba(178, 171, 165, 0.9);
        }
        &:hover{
            background: #01add9;
            .owl-numbers{
                box-shadow: none;
            }
        }
    }
    .active{
        background: #01add9;
        .owl-numbers{
            line-height: 28px;
            padding: 0 11px;
            box-shadow: none;
        }
    }
    .carousel_container{  
        margin: 0;
        padding: 0;
        border: 0px solid #000;
        border-radius: 10px;
    }
    .owl-wrapper-outer{
        border: 0px solid #000;
        border-radius: 10px;
    }
    .owl-pagination{
        position: absolute;
        bottom: 10px;
        right: 120px;
    }
}

