#tabs{
    display: block;
    ul{
        list-style: none;
        border: 1px solid #CFCDC5;
        border-radius: 6px;
        display: table;
        margin: 0;
        padding: 0;
        width: 100%;
        li{
            display: table-cell;
            border: #CFCDC5 1px solid;
            text-align: center;
            vertical-align: top;
            border-radius: 3px;
            cursor: pointer;
            background: #ffffff;
            background: -moz-linear-gradient(top,  #ffffff 0%, #ffffff 58%, #f5f5f3 81%, #f3f2f0 94%, #f4f3f1 97%, #fffffd 100%);
            background: -webkit-linear-gradient(top,  #ffffff 0%,#ffffff 58%,#f5f5f3 81%,#f3f2f0 94%,#f4f3f1 97%,#fffffd 100%);
            background: linear-gradient(to bottom,  #ffffff 0%,#ffffff 58%,#f5f5f3 81%,#f3f2f0 94%,#f4f3f1 97%,#fffffd 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#fffffd',GradientType=0);
            a{
                text-decoration: none;
                font: 18px/24px "Trebuchet MS",Arial,Helvetica,sans-serif;
                color: #444137;
                padding: 10px 0;
                display: block;
            }
        }
        .ui-state-active,
        .ui-state-focus{
            background: #ee1c05;
            background: -moz-linear-gradient(top, #ee1c05 0%, #e11703 26%, #d71104 41%, #d00f00 50%, #c90d04 62%, #c40902 68%, #bf0804 79%, #ba0401 91%, #b60402 94%, #b60402 100%);
            background: -webkit-linear-gradient(top, #ee1c05 0%,#e11703 26%,#d71104 41%,#d00f00 50%,#c90d04 62%,#c40902 68%,#bf0804 79%,#ba0401 91%,#b60402 94%,#b60402 100%);
            background: linear-gradient(to bottom, #ee1c05 0%,#e11703 26%,#d71104 41%,#d00f00 50%,#c90d04 62%,#c40902 68%,#bf0804 79%,#ba0401 91%,#b60402 94%,#b60402 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ee1c05', endColorstr='#b60402',GradientType=0 );
            border: #9A0303 solid 1px;
            box-shadow: inset 1px 1px 3px 1px rgba(249, 158, 147, 0.9);
            -moz-box-shadow: inset 1px 1px 3px 1px rgba(249, 158, 147, 0.9);
            -webkit-box-shadow: inset 1px 1px 3px 1px rgba(249, 158, 147, 0.9);
            a{
                color: #fff;
            }
        }
    }
    &>div{
        padding: 21px 0;
    }
    .post_list{
        border-radius: none;
        border: none;
        display: inline-block;
        li{
            display: inline-block;
            width: 31.20567375886525%;
            border-radius: none;
            padding: 0 4px 0 0;
            margin: 0 16px 15px 0;
            border: none;
            cursor: auto;
            text-align: left;
            background: #fff;
        }
        img{
            border: 5px solid #dbdbda;
            border-radius: 5px;
        }
        & li:nth-child(3n+3){
            margin: 0;
        }

    }
}
