            border-radius: 5px 5px 0 0;
            background: #e7e7e7;
            background: -moz-linear-gradient(top, #e7e7e7 0%, #e7e7e7 13%, #dbdbdb 50%, #d7d7d7 53%, #d2d2d2 63%, #c8c8c8 73%, #c4c4c4 80%, #aaaaaa 100%);
            background: -webkit-linear-gradient(top, #e7e7e7 0%,#e7e7e7 13%,#dbdbdb 50%,#d7d7d7 53%,#d2d2d2 63%,#c8c8c8 73%,#c4c4c4 80%,#aaaaaa 100%);
            background: linear-gradient(to bottom, #e7e7e7 0%,#e7e7e7 13%,#dbdbdb 50%,#d7d7d7 53%,#d2d2d2 63%,#c8c8c8 73%,#c4c4c4 80%,#aaaaaa 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e7e7e7', endColorstr='#aaaaaa',GradientType=0 );
            a{
